import React, {
	Component
} from 'react';
import './imageInfo.css';

class ImageInfo extends Component {
	constructor(props) {
		super(props);
		this.state = {
		};
	}

	render(){
		return (
			<div className="info-wrap">
				<div className="info-bar">
					在您的图像中找到大约<span className="info">{this.props.processInfo.colors}</span>种颜色.
				</div>
				<div className="info-bar">
					颜色查询时间为 <span className="info">{this.props.processInfo.censusTime}</span> 毫秒.
				</div>
				<div className="info-bar">
					k-均值为 <span className="info">{this.props.processInfo.kmeansIteration}</span> 次迭代，开销为 <span className="info">{this.props.processInfo.kmeansTime}</span>ms.
				</div>
				<div className="info-bar">
					前5种颜色占总数的 <span className="info">{this.props.processInfo.top5Count.toFixed(1)}%</span>.
				</div>
			</div>
		);
	}
}

export default ImageInfo;


